@tailwind base;
@tailwind components;
@tailwind utilities;

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

.thought-bubble {
  position: absolute;
  bottom: 130%; /* Position above the heart */
  left: 50%;
  transform: translateX(-50%) translateY(50%);
  background-color: white;
  border: 2px solid black;
  border-radius: 25px; /* This makes the bubble round */
  padding: 15px;
  width: max-content;
  max-width: 80%; /* Adjust as needed */
  text-align: center;
  font-size: 1rem;
  z-index: 10;
  /* Tailwind classes equivalent for font and other styles can be used */
}

.thought-bubble::before,
.thought-bubble::after {
  content: "";
  position: absolute;
  bottom: -20px; /* Adjust as needed */
  left: 50%;
  background-color: white;
  border: 4px solid black;
  border-radius: 100%; /* Makes the dots circular */
  z-index: 9;
}

.thought-bubble::before {
  width: 20px; /* Size of the first thought dot */
  height: 20px;
  transform: translate(-50%, 0);
}

.thought-bubble::after {
  width: 30px; /* Size of the second thought dot */
  height: 30px;
  transform: translate(-50%, 20px); /* Adjust as needed */
}

.gallery-container {
  max-width: 1200px;
  margin: auto;
  padding: 20px;
}

.image-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-gap: 20px;
}

.image-card {
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.3s ease;
}

.image-card:hover {
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
}

.gallery-image {
  width: 100%;
  height: auto;
  display: block;
  transition: transform 0.3s ease;
}

.gallery-image:hover {
  transform: scale(1.05);
}

.image-info {
  padding: 10px;
}

.image-title {
  font-weight: bold;
}

.pagination {
  display: flex;
  justify-content: center;
  padding: 20px;
}

.page-button {
  margin: 0 5px;
  padding: 5px 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.page-button.active {
  background-color: #007bff;
  color: white;
}

.page-button:hover {
  background-color: #0056b3;
  color: white;
}

.gallery-container {
  max-width: 1200px;
  margin: auto;
  padding: 20px;
  text-align: center; /* Center the title */
}

h2 {
  font-size: 2.5rem; /* Larger font size */
  font-weight: bold; /* Make it bold */
  color: #333; /* Darker color for better readability */
  margin-bottom: 30px; /* More space below the title */
  position: relative;
  display: inline-block;
  padding-bottom: 10px;
}

h2::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  height: 4px;
  width: 50%; /* Line width */
  background-color: #007bff; /* Line color */
  border-radius: 2px;
  transition: width 0.3s ease;
}

h2:hover::after {
  width: 100%; /* Expand underline on hover */
}

.fade-in {
  animation: fadeIn 0.5s;
}

.fade-out {
  animation: fadeOut 0.5s;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.home-button {
  position: fixed; /* Fixed position */
  top: 20px; /* Position from the top */
  left: 20px; /* Position from the left */
  background-color: #ff4081; /* Pink background */
  color: white; /* White text */
  border: none; /* No border */
  border-radius: 50%; /* Circular button */
  padding: 10px 15px; /* Padding */
  cursor: pointer; /* Pointer cursor on hover */
  display: flex; /* Flex display */
  align-items: center; /* Center items vertically */
  justify-content: center; /* Center items horizontally */
  font-size: 1.2em; /* Larger font size */
}

.home-button:hover {
  background-color: #e03567; /* Darker shade on hover */
}

/* Media query for mobile devices */
@media (max-width: 767px) {
  .home-button {
    /* Adjust size for mobile */
    padding: 5px 10px; /* Smaller padding */
    font-size: 0.8em; /* Smaller font size */
    /* Adjust any other properties like width, height, etc., as needed */
  }
}
